import React, { useEffect, useState } from 'react';
import cc from 'classcat';
import { IconArtType } from './IconArt.types';
import './IconArt.css';

export interface IconProps extends React.HTMLAttributes<HTMLSpanElement> {
  icon: IconArtType;
  size?: 'sm' | 'md' | 'lg';
  themeColor?: 'light' | 'dark';
}

export const IconArt = ({ icon, size = 'md', themeColor = 'light', ...restProps }: IconProps) => {
  const [LazyIcon, setLazyIcon] = useState(null);

  useEffect(() => {
    const importIcon = async () => {
      try {
        const { default: importedIcon } = await import(`./icons/${icon}-art-${themeColor}.svg`);
        setLazyIcon(importedIcon);
      } catch (error) {
        console.warn(`Failed to load icon: \`${icon}\``);
      }
    };
    importIcon();
  }, [icon, themeColor]);

  if (!LazyIcon) {
    return null;
  }

  return (
    <span className={cc(['IconArt_root', `icon--${size}`])} {...restProps}>
      {LazyIcon}
    </span>
  );
};
